@tailwind base;
@tailwind components;
@tailwind utilities;

body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

/* CARDS Styles */
/* Additional styling for hover effects, active state, etc., can be added here */

.cards-container {
  display: flex;
  flex-wrap: wrap;
  justify-content: space-between; /* Adjust as needed for spacing */
  margin: 0 auto;
  padding: 20px;
}

.card {
  width: calc(50% - 10px); /* Adjusts for 2 cards per row with a little gap */
  margin-bottom: 20px; /* Space between rows */
}

.card-image {
  width: 100%;
  height: auto; /* Maintain aspect ratio */
  display: block; /* Removes bottom space under the image */
  margin: 0 auto; /* Center the image */
  border-radius: 8px; /* Optional: Adds rounded corners to the image */
}

.card a {
  font-size: 12px;
  text-align: left; /* Center-align the title */
  margin-top: 10px; /* Space between the image and the title */
  text-decoration: none; /* Optional: removes underline from links */
  color: #000000; /* Example link color, adjust as needed */
}

.card-view {
  display: flex;
  flex-direction: column;
  justify-content: space-between; /* Space out the content and the button */
  align-items: left; /* Center content vertically */
}

.card-image-container {
  width: 100%; /* Ensure the image container takes up the full width */
}

.card-image {
  width: 100%;
  max-width: 250px;; /* Make the image responsive */
  height: auto; /* Maintain aspect ratio */
  display: block;
}
.card-image-view {
  width: 100%;
  border-radius: 8px;
  height: auto; /* Maintain aspect ratio */
  display: block;
}

.card-content {
  text-align: left;
  padding-bottom: 70px; /* Increased bottom padding to prevent button overlay */
  overflow-y: auto; /* Enables scrolling within the content area if needed */
}

.card-action {
  width: 97%;
  margin-bottom: 24px ; /* Center the navbar */
  border-radius: 50px;
  height: 50px;;
  background-color: #007bff; /* Example button color, adjust as needed */
  text-align: center;
  position: sticky;
  bottom: 0;
  display: flex;
  align-items: center; /* Center content vertically */
  justify-content: center; /* Center content horizontally if needed */
  top: 20px;
}
.bottom-spacing {
  height: 20px; /* Simulates bottom margin for the sticky element */
}

.learn-more-btn {
  text-align: center; /* Center the navbar content */
  font-size: 16px;
  color: #000000; /* Button text color */
  text-decoration: none;
  transition: background-color 0.3s ease; /* Smooth transition for hover effect */
}

.learn-more-btn:hover {
  background-color: #0056b3; /* Darker shade on hover */
}

.btn-edit, .btn-delete {
  margin: 5px;
  padding: 5px 10px;
  border: none;
  border-radius: 5px;
  cursor: pointer;
}
.add-new-card{
  background-color: #fbfbfb;
  border: 3px;
  border-style: solid;
  border-color: #eeeeee;
  text-align: center;
  height: 200px;
  border-radius: 8px;
  align-items: center;
  justify-content: center;
  display: flex;
}
